document.addEventListener('DOMContentLoaded', function(){
    startApp();
});

function startApp(){
    fixedNavigation();
    createGalery();
    scrollNav();
}

function fixedNavigation(){
    let bar = document.querySelector('.header');
    let aboutRoom = document.querySelector('.about-room');
    let body = document.querySelector('body')

    window.addEventListener('scroll', function(){

        if(aboutRoom.getBoundingClientRect().top < 0 ){
            bar.classList.add('fix')
            body.classList.add('body-scroll')
        }else {
            bar.classList.remove('fix')
            body.classList.remove('body-scroll')
        }
    })
}

function scrollNav(){
    let links = document.querySelectorAll('.main-navigation a')
    
    links.forEach(link => {
        link.addEventListener('click', function(e) {
            e.preventDefault()

            let sectionScroll = e.target.attributes.href.value
            let section = document.querySelector(sectionScroll)
            section.scrollIntoView({behavior: "smooth"})
        })
    })
}

function createGalery(){
    let galery = document.querySelector('.image-galery');

    for(let i = 1; i<=12; i++){
        let image = document.createElement('picture');

        image.innerHTML = `
            <source srcset="build/img/thumb/${i}.avif" type="imagen/avif">
            <source srcset="build/img/thumb/${i}.webp" type="imagen/webp">
            <img loading="lazy" width="200" height="300" src="build/img/thumb/${i}.jpg" alt="">
        `;

        image.onclick = function(){
            showImage(i);
        }

        galery.appendChild(image)
    }

}

function showImage(id){
    let image = document.createElement('picture');

    image.innerHTML = `
        <source srcset="build/img/big/${id}.avif" type="imagen/avif">
        <source srcset="build/img/big/${id}.webp" type="imagen/webp">
        <img loading="lazy" width="200" height="300" src="build/img/big/${id}.jpg" alt="">
    `;

    // Create the Overlay with the image
    let overlay = document.createElement('DIV');
    overlay.appendChild(image);
    overlay.classList.add('overlay');
    overlay.onclick = function(){
        let body = document.querySelector('body');
        body.classList.remove('fix-body');
        overlay.remove()
    }

    // Botton to close the modal
    let closeModal = document.createElement('P');
    closeModal.textContent = 'X';
    closeModal.classList.add('btn-close');
    closeModal.onclick = function(){
        let body = document.querySelector('body');
        body.classList.remove('fix-body');
        overlay.remove()
    }
    overlay.appendChild(closeModal);

    // Add HTML
    let body = document.querySelector('body');
    body.appendChild(overlay);
    body.classList.add('fix-body');

}